<template>
  <div class="f5">
    <van-nav-bar title="电费查询" />   
      <van-list
        v-model="loading"
        :finished="finished"
        :immediate-check="false"
        finished-text="没有更多了"
        @load="onLoad"
        :offset="10"
      >
        <van-cell-group v-for="(item,index) in eleclist" :key="index">
          <van-cell title="房间号" :value="item.room_num" />
          <van-cell title="总金额" :value="item.allmoney" />
          <van-cell
            v-for="itson in item.cost_detail"
            :key="itson.person_name"
            :title="itson.person_name"
            :value="itson.elec_cost"
          />
          <van-cell title="时间" :value="item.meter_date" />
        </van-cell-group>
      </van-list>    
  </div>
</template>

<script>
import { apieleclist } from "@/api/index";
export default {
  name: "electricityCost",
  data() {
    return {
      loading: false,
      finished: false,
      page: 1, //请求第几页
      pageSize: 5, //每页请求的数量
      total: 0, //总共的数据条数
      eleclist: []
    };
  },
  methods: {
    // 上拉加载
    onLoad() {
      this.page++;
      this.getList();
    },
    //获取列表
    getList() {
      let params = {
        page: this.page,
        pageSize: this.pageSize
      };
      apieleclist(params).then(res => {
        this.loading = false;
        this.total = res.data.data.count; 
        let rows = res.data.data.info
        if (res.data.data.info == null || rows.length === 0) {
          // 加载结束
          this.finished = true;
          return;
        }
        this.eleclist = this.eleclist.concat(rows);
         this.isLoading = false;
         if (this.eleclist.length >= this.total) {
            this.finished = true;
         }
      });
    },
  },
  created() {
    this.getList();
  },
};
</script>

<style>
</style>


